import React from "react";
import "./MessageBox.scss";
import { RocketChatService } from "../RocketChatService";

interface Props {
  rocketchat: RocketChatService;
  roomId: string;
  onUpdate: () => any;
}
interface State {}

export class MessageBox extends React.Component<Props, State> {
  async onKeyPress(event: React.KeyboardEvent<HTMLDivElement>) {
    if (event.key === "Enter") {
      const message = event.currentTarget.innerText;
      event.currentTarget.innerText = "";
      await this.props.rocketchat.call("POST", "chat.postMessage", {
        roomId: this.props.roomId,
        text: message,
      });
      this.props.onUpdate();
    }
  }

  render() {
    return (
      <div
        className="ChatMessage MessageBox"
        contentEditable={true}
        onKeyPress={async (event) => await this.onKeyPress(event)}
        onClick={() => this.props.onUpdate()}
      ></div>
    );
  }
}
